.food {
    background: #F7F9FE;
    &__wrapper {
        display: flex;
        flex-direction: column;
        
        border-top: 1px solid $color-lightgrey;
        border-bottom: 1px solid $color-brd-lightgrey;
    }
    &__title {
        max-width: 19rem;
        margin-bottom: 2.7rem;
    }

    &__list {
        display: flex;
    }

    &__card {
        width: 25%;
        margin-right: 2rem;

        img {
            display: block;
            width: 100%;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        font-size: 1.7rem;
        line-height: 2rem;
        
        height: 4rem;
        color: $color-blue;
        margin-left: auto;
    }
}