.company {
    background: #1F2126;
    border-radius: 10px;

    &__wrapper {
        padding: 2.7rem 0 3.5;
    }

    &__title {
        margin-bottom: 1.1rem;
    }

    &__desc {
        margin-bottom: 3.4rem;
        max-width: 28rem;
    }

    &__link {
        display: block;
        text-align: center;
        width: 100%;
        font-size: 1.6rem;
        line-height: 1.9rem;

        padding: 1.5rem 0.5rem;
        background-color: transparent;
        cursor: pointer;
        border: 1px solid $color-white;
        border-radius: 8px;

        color: $color-white;
    }
}